import React, { useState } from "react";
import elipic from "../../assets/images/Ellipse 24.png";
import Group16 from "../../assets/images/Group 16.png";
import Group17 from "../../assets/images/Group 17.png";
import CustomModal from "../custommodal";
import SuccessModal from "../common/modal/success";
import VFM_Avatar from "../common/avatar";
import { EditOutlined, DeleteOutlined } from '@ant-design/icons';
import { Button } from 'antd';
import { useTranslation } from "react-i18next";
import RegFormModal from "../common/modal/RegForm";

const Table = ({ tableData }) => {
    const [t] = useTranslation();
    const [open, setOpen] = useState(false);
    const [popupdata, setPopupdata] = useState("");
    const handleOpen = () => setOpen(true);
    const handleClose = () => setOpen(false);

    const [openConfirm, setOpenConfirm] = useState(false);
    const handleConfirm = () => setOpenConfirm(true);

    const [openForm, setOpenForm] = useState(false);
    const [open1, setOpen1] = useState(false);
    const hadnleOpenForm = () => setOpenForm(true);
    const hadnleCloseForm = () => setOpenForm(false);

    const handleOpen1 = () => setOpen1(true);
    const handleClose1 = () => setOpen1(false);
    const [open2, setOpen2] = useState(false);
    const handleOpen2 = () => setOpen2(true);
    const handleClose2 = () => setOpen2(false);
    const closefun = () => {
        hadnleCloseForm();
        handleOpen1();
    };
    return (
        <>
            <div className="py-12">
                <h1 className="text-sm w-48 left-0 relative right-0 ml-auto border-b-2 border-badge pb-1 text-right m-2">リクエスター総数　100名</h1>
                <div className="shadow-lg shadow-gray-400 border-none">
                    <div id="header" className="header-admin-table">
                        <h1 className="w-1/5 text-20 p-5 font-semibold">ユーザー名</h1>
                        <h1 className="w-1/10 text-20 font-semibold">登録日</h1>
                        <h1 className="w-1/20 text-20 font-semibold">氏</h1>
                        <h1 className="w-1/20 text-20 font-semibold">名</h1>
                        <h1 className="w-1/5 text-20 font-semibold">メールアドレス</h1>
                        <h1 className="w-1/10 text-20 font-semibold">パスワード</h1>
                        <h1 className="w-3/10 text-20 font-semibold w-30"></h1>
                    </div>
                    {tableData.map((data, index) => (
                        <div key={index} className="header-admin-table-1">
                            <div className="w-1/5 flex items-center gap-4">
                                <VFM_Avatar img={elipic} size={16} table />
                                <h1>{data.username}</h1>
                            </div>
                            <h1 className="w-1/10">{data.registrationDate}</h1>
                            <h1 className="w-1/20">{data.lastName}</h1>
                            <h1 className="w-1/20">{data.firstName}</h1>
                            <h1 className="w-1/5">{data.email}</h1>
                            <h1 className="w-1/10">{data.password}</h1>
                            <div className="flex justify-center gap-4 p-3">
                                <Button type="primary" icon={<EditOutlined />} onClick={() => { hadnleOpenForm() }} >{t("更新")}</Button>
                                <Button type="primary" danger icon={<DeleteOutlined />} onClick={() => { handleOpen2() }} >{t("削除")}</Button>
                            </div>
                        </div>
                    ))}

                </div>
            </div>
            <RegFormModal open={openForm} onCancel={hadnleOpenForm} onConfirm={closefun} />
            <SuccessModal open={open1}
                onCancel={handleClose1}
                title="プロフィールを編集しました。"
            />
            <SuccessModal open={open2}
                onCancel={handleClose2}
                title="プロフィールを削除しました。"
            />
        </>
    );
}

export default Table;